/* global.css */
html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
  background-color: #E5E8EB; /* Light Background */
  color: #22252B; /* Dark Text Color */
  padding-top: 64px; /* Space for fixed navbar */
}

.container {
  width: 80%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: underline;
}

section {
  padding: 60px 0; /* Add space between sections */
}

/* Background colors for sections */
#home {
  background-color: #E5E8EB; /* Light Background */
}

#about {
  background-color: #A3AEB1; /* Secondary Accent */
}

#projects {
  background-color: #B09F95; /* Highlight Color */
}

#contact {
  background-color: #53655C; /* Primary Accent */
}

/* Navbar Styles */
.navbar {
  background-color: rgba(34, 37, 43, 0.8); /* Semi-transparent Dark Background */
}
